import classes from './NoQuotesFound.module.css';
import { Link } from 'react-router-dom';

const NoQuotesFound = () => {
  return (
    <div className={classes.noquotes}>
      <p>Daftar quote kosong!</p>
      <Link className='btn' to='/quotes/new-quote'>
        Tambah quote baru
      </Link>
    </div>
  );
};

export default NoQuotesFound;
